import React, { useState } from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../store';
import { updateSquatCount } from '../store/squatSlice';
import { saveSquatDataOnChain } from '../services/solanaService';
import VideoComponent from '../components/VideoComponent';
import RewardModal from '../components/RewardModal';
import { useWallet } from '@solana/wallet-adapter-react';

const Container = styled.div`
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
`;

const Title = styled.h1`
  color: ${props => props.theme.colors.primary};
  margin-bottom: 20px;
`;

const SquatChallenge: React.FC = () => {
  const dispatch = useDispatch();
  const wallet = useWallet(); // ‚úÖ useWallet()ÏùÑ Ïª¥Ìè¨ÎÑåÌä∏ ÎÇ¥Î∂ÄÏóêÏÑú Ìò∏Ï∂ú (Ïò§Î•ò Ìï¥Í≤∞)
  const { todayCount, dailyGoal } = useSelector((state: RootState) => state.squats);
  const [showReward, setShowReward] = useState(false);

  const handlePoseDetected = async ({ isSquatting }: { isSquatting: boolean }) => {
    if (!isSquatting) {
      const newCount = todayCount + 1;
      dispatch(updateSquatCount(1)); // ‚úÖ Redux ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏

      // ‚úÖ 10ÌöåÎßàÎã§ Solana Î∏îÎ°ùÏ≤¥Ïù∏Ïóê Ï†ÄÏû•
      if (newCount % 10 === 0) {
        try {
          if (!wallet || !wallet.publicKey) {
            alert('‚ùå ÏßÄÍ∞ëÏù¥ Ïó∞Í≤∞ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§. Î∏îÎ°ùÏ≤¥Ïù∏ Ï†ÄÏû•Ïù¥ Î∂àÍ∞ÄÎä•Ìï©ÎãàÎã§.');
            return;
          }

          const txSignature = await saveSquatDataOnChain(wallet.publicKey.toString(), newCount);
          console.log(`‚úÖ Î∏îÎ°ùÏ≤¥Ïù∏ Ï†ÄÏû• ÏôÑÎ£å: ${txSignature}`);
        } catch (error) {
          console.error('‚ùå Î∏îÎ°ùÏ≤¥Ïù∏ Ï†ÄÏû• Ïã§Ìå®:', error);
        }
      }

      // ‚úÖ Î™©Ìëú Îã¨ÏÑ± Ïãú Î≥¥ÏÉÅ ÌôîÎ©¥ ÌëúÏãú
      if (newCount >= dailyGoal) {
        setShowReward(true);
      }
    }
  };

  return (
    <Container>
      <Title>üèãÔ∏è‚Äç‚ôÇÔ∏è Ïä§ÏøºÌä∏ Ï±åÎ¶∞ÏßÄ</Title>
      <VideoComponent onPoseDetected={handlePoseDetected} />
      {showReward && <RewardModal onClose={() => setShowReward(false)} onSave={async () => console.log('Î≥¥ÏÉÅ Ï†ÄÏû•!')} />}
    </Container>
  );
};

export default SquatChallenge;
