import { Connection, PublicKey, Transaction, SystemProgram, sendAndConfirmTransaction } from '@solana/web3.js';

// ‚úÖ Solana ÎÑ§Ìä∏ÏõåÌÅ¨ ÏÑ§Ï†ï (Devnet, Testnet, Mainnet ÏÑ†ÌÉù Í∞ÄÎä•)
const SOLANA_NETWORK = process.env.REACT_APP_SOLANA_NETWORK || 'https://api.devnet.solana.com';
const connection = new Connection(SOLANA_NETWORK, 'confirmed');

// ‚úÖ Ïä§ÎßàÌä∏ Ïª®Ìä∏ÎûôÌä∏ (ÌîÑÎ°úÍ∑∏Îû®) ID (SolanaÏóê Î∞∞Ìè¨Ìïú ÌîÑÎ°úÍ∑∏Îû®Ïùò IDÎ•º ÏûÖÎ†•Ìï¥Ïïº Ìï®)
const PROGRAM_ID = new PublicKey('YourProgramIDHere');

// ‚úÖ Solana Ìä∏ÎûúÏû≠ÏÖòÏùÑ ÏÉùÏÑ±ÌïòÍ≥† Î∏îÎ°ùÏ≤¥Ïù∏Ïóê Ï†ÄÏû•ÌïòÎäî Ìï®Ïàò
export const saveSquatDataOnChain = async (wallet: any, squatCount: number) => {
  if (!wallet || !wallet.publicKey) {
    throw new Error('ÏßÄÍ∞ëÏù¥ Ïó∞Í≤∞ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
  }

  try {
    const userPublicKey = new PublicKey(wallet.publicKey);
    const instructionData = Buffer.from(
      JSON.stringify({ squatCount, timestamp: Date.now() }) // ‚úÖ Ïö¥Îèô Îç∞Ïù¥ÌÑ∞Î•º JSON ÌòïÌÉúÎ°ú Î≥ÄÌôò
    );

    const transaction = new Transaction().add(
      SystemProgram.transfer({
        fromPubkey: userPublicKey,
        toPubkey: PROGRAM_ID, // ‚úÖ Ïä§ÎßàÌä∏ Ïª®Ìä∏ÎûôÌä∏ Ï£ºÏÜåÎ°ú Îç∞Ïù¥ÌÑ∞ Ï†ÑÏÜ°
        lamports: 0, // ‚úÖ Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•Ïù¥ÎØÄÎ°ú SOL Ï†ÑÏÜ° ÏóÜÏùå
      })
    );

    // ‚úÖ ÏÇ¨Ïö©ÏûêÏùò ÏßÄÍ∞ëÏùÑ ÌÜµÌï¥ Ìä∏ÎûúÏû≠ÏÖò ÏÑúÎ™Ö Î∞è Ï†ÑÏÜ°
    const signature = await wallet.signAndSendTransaction(transaction);
    console.log(`‚úÖ Ìä∏ÎûúÏû≠ÏÖò ÏÑ±Í≥µ! Ï°∞Ìöå ÎßÅÌÅ¨: https://explorer.solana.com/tx/${signature}?cluster=devnet`);
    
    return signature;
  } catch (error) {
    console.error('‚ùå Solana Ìä∏ÎûúÏû≠ÏÖò Ïã§Ìå®:', error);
    throw error;
  }
};
